import React, { Component } from 'react';
import {UserContext} from '../UserContext';
import CalendarNew from '../Calendar/Calendar';
// import Modal from '../Modal/Modal'
import {NavLink} from 'react-router-dom';
import './AddCombine.css';

class AddCombine extends Component {
    static contextType = UserContext;
    render(){
        return(
            <div className = {this.context.user.addCombShow ? 'addCombineShow' : 'addCombineClose'}>
                {/*<Modal>*/}
                <form onSubmit={(e) => {this.context.handleSubmitComb(e); this.context.addNewCombine()}}>
                <ul className="list">
                <li><span>Страна</span><input className="listItem" placeholder = 'Страна...' name="country" type="text" required onChange={this.context.handleChange} value={this.context.user.country}/>
                    {/*<select name='category'>*/}
                        {/*<option>Украина</option>*/}
                        {/*<option>Казахстан</option>*/}
                        {/*<option>Белоруссия</option>*/}
                        {/*<option>Америка</option>*/}
                    {/*</select>*/}
                </li>
                    <li><span>Клиент</span><input className="listItem" name="client" placeholder = 'Клиент...' type="text" required onChange={this.context.handleChange} value={this.context.user.client}/></li>
                <li><span>Шахта</span><input className="listItem" placeholder = 'Шахта...' name="mine" type="text" required onChange={this.context.handleChange} value={this.context.user.mine}/></li>
                <li><span>Место установки</span><input className="listItem" placeholder = 'Место установки...' name="install" required type="text" onChange={this.context.handleChange} value={this.context.user.install}/></li>
                <li><span>Название комбайна</span><input className="listItem" placeholder = 'Название комбайна...' name="combName" required type="text" onChange={this.context.handleChange} value={this.context.user.combName}/></li>
                <li><span>Заводской номер</span><input className="listItem" placeholder = 'Заводской номер...' name="combNumber" required type="text" onChange={this.context.handleChange} value={this.context.user.combNumber}/></li>
                <li><span>Дата установки</span><CalendarNew startDate={this.context.user.date} handleChangeInstallDate={this.context.handleChangeInstallDate}/></li>
                {/*<li><span>Дата установки</span><input className="listItem" placeholder = 'Дата установки...' name="installDate" required type="text" onChange={this.context.handleChange} value={this.context.user.installDate}/></li>*/}
                <li><span>Условия гарантии</span><input className="listItem" placeholder = 'Условия гарантии...' name="warrantyTerm" required type="text" onChange={this.context.handleChange} value={this.context.user.warrantyTerm}/></li>
                {/*<li><span>Дата начала Эксплуатации</span><CalendarNew startDate={this.context.user.date} handleChangeInstallDate={this.context.handleChangeInstallDate}/></li>*/}
                <li><span>Дата начала Эксплуатации</span><input className="listItem" placeholder = 'Дата начала эксплуатации...' required name="startOperationDate" type="text" onChange={this.context.handleChange} value={this.context.user.startOperationDate}/></li>
                </ul>
                <button className = 'btnAddCombine'>Добавить</button>
                </form>
            {/*<NavLink to="/mainpage">*/}
                {/*<button className = 'btnAddCombine'>Вернуться</button>*/}
            {/*</NavLink>*/}
                {/*</Modal>*/}
            </div>
        )
    }
}

export default AddCombine;





